@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .footerIcon {
    @apply w-7 h-7 rounded-full bg-gray-800 hover:bg-gray-900 cursor-pointer duration-500 text-gray-400 hover:text-white border border-transparent hover:border-gray-500 flex justify-center items-center;
  }
  .categoryP {
    @apply w-full h-1/3 border-[1px] border-gray-500 text-gray-300 hover:text-white duration-300 flex items-center justify-center cursor-pointer rounded-md;
  }
}

.scrollbar-hide::-webkit-scrollbar {
  display: none;
}
